import React, { Component } from 'react';
import PropTypes from 'prop-types';

class Icon extends Component {
  render(){

    return(
      <div className="container">

        {/* XX: Temporary use of placeholder star icon */}
        <div onClick={()=>console.log('Yay')}>
          <img className="small-star-yellow" src="https://upload.wikimedia.org/wikipedia/commons/thumb/2/29/Gold_Star.svg/2000px-Gold_Star.svg.png"/>
        </div>
        
        <div onClick={()=>console.log('Yay')}>
          <img className="small-star-grey" src="http://www.gamingevolution.info/screenshots/main.php/d/149898-1/Greystar.gif"/>
        </div>

        <style jsx>{`
          .small-star-yellow {
            display: ${this.props.isFavorited ? "flex" : "none"};
            max-width: 32px;
          }
          .small-star-grey {
            display: ${this.props.isFavorited ? "none" : "flex"};
            max-width: 32px;
          }
        `}
        </style>
      </div>
    );
  }
}

Icon.propTypes = {
  id: PropTypes.string,
  clickAction: PropTypes.func,
  pressed: PropTypes.bool,
  iconUrl: PropTypes.string,
};

export default Icon;


